import type { BiFactory } from '@ali/4ever-cangjie';
import { moFactory } from '../mo/index';

import toolbar from './toolbar';
import { commands } from './commands';
import { queries } from './queries';
import onAction from './handlers/onAction';
import { actions, isActions } from './actions';
import createRenderToolbar from './createRenderToolbar';

export const biFactory: BiFactory = (configs) => {
  const colorConfig = configs.color || {};

  return {
    ...moFactory({}),
    actions,
    isActions,
    toolbar: toolbar(colorConfig),
    renderToolbar: createRenderToolbar(colorConfig.locale as any),
    commands,
    queries,
    onAction,
  };
};
